.address__map {
  position: absolute;
  top: 0;
  left: 0;
  border: none;
}
@media (max-width: 1023px) and (max-width: 1023px) {
  .address__map {
    grid-template-columns: 324px 1fr;
    width: 100%;
    height: 335px;
  }
}
@media (max-width: 767px) and (max-width: 767px) {
  .address__map {
    width: 100%;
    height: 316px;
  }
}

.address__map-marker {
  position: absolute;
  top: 249px;
  left: 217px;
}
@media (max-width: 767px) and (max-width: 767px) {
  .address__map-marker {
    top: 224px;
    left: 112px;
  }
}

.address__info-wrapper {
  display: flex;
  flex-direction: column;
  padding: 75px 0 0 0;
}
@media (max-width: 1023px) and (max-width: 1023px) {
  .address__info-wrapper {
    padding: 11px 0 0 0;
    align-self: center;
  }
}
@media (max-width: 767px) and (max-width: 767px) {
  .address__info-wrapper {
    padding: 0;
  }
}

@media (max-width: 1023px) and (max-width: 1023px) {
  .address__info {
    width: 88%;
  }
}

.address__info-wrapper p {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 18px;
  line-height: 150%;
  font-weight: 500;
  margin: 0 0 32px 0;
}
@media (max-width: 1023px) and (max-width: 1023px) {
  .address__info-wrapper p {
    margin: 0 0 29px 0;
  }
}
@media (max-width: 767px) and (max-width: 767px) {
  .address__info-wrapper p {
    margin: 0 0 27px 0;
  }
}

.address__info span {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 150%;
  font-weight: 400;
  display: block;
  font-style: normal;
  color: #011c40;
  margin: 0 0 22px 0;
  padding-left: 28px;
  position: relative;
}
.address__info span::before {
  content: "";
  position: absolute;
  top: 5px;
  left: 0;
  width: 18px;
  height: 22px;
  background: url("../img/svg/marker.svg") no-repeat center;
}
@media (max-width: 1023px) and (max-width: 1023px) {
  .address__info span {
    margin: 0 0 20px 0;
  }
}
@media (max-width: 767px) and (max-width: 767px) {
  .address__info span {
    margin: 0 0 21px 0;
  }
}

.address__info a {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 150%;
  font-weight: 400;
  cursor: pointer;
  display: inline-block;
  font-style: normal;
  color: #011c40;
  padding-left: 28px;
  position: relative;
}
.address__info a::before {
  content: "";
  position: absolute;
  top: 2px;
  left: 0;
  width: 18px;
  height: 18px;
  background: url("../img/svg/phone.svg") no-repeat center;
}
@media (max-width: 1023px) and (max-width: 1023px) {
  .address__info a {
    margin: 0 0 12px 0;
  }
}
@media (max-width: 767px) and (max-width: 767px) {
  .address__info a {
    margin: 0 0 36px 0;
  }
}
@media (hover: hover) and (hover: hover) {
  .address__info a:hover:not(.focus-visible):hover:not(.focus-visible) {
    color: #008cff;
  }
}
.address__info a.focus-visible:active.focus-visible:active {
  color: #005eac;
}
.address__info a.focus-visible:focus.focus-visible:focus {
  outline: 1px solid #0161b0;
}

.footer {
  background-color: #011c40;
}

.footer .container {
  display: flex;
  align-items: center;
  padding: 50px 20px;
}
@media (max-width: 1023px) and (max-width: 1023px) {
  .footer .container {
    padding: 42px 45px 46px 45px;
  }
}
@media (max-width: 767px) and (max-width: 767px) {
  .footer .container {
    padding: 40px 15px;
    flex-direction: column;
    align-items: flex-start;
  }
}

.footer__logo {
  display: block;
  outline: none;
  margin-right: 40px;
  cursor: default;
}
@media (max-width: 1023px) and (max-width: 1023px) {
  .footer__logo {
    margin-right: 25px;
  }
}
@media (max-width: 767px) and (max-width: 767px) {
  .footer__logo {
    margin: 0 0 24px 0;
  }
}
.footer__logo.focus-visible:focus.focus-visible:focus {
  outline: 2px solid #0092bb;
}

@media (max-width: 1023px) and (max-width: 1023px) {
  .footer__logo img {
    width: 38px;
    height: 32px;
  }
}
@media (max-width: 767px) and (max-width: 767px) {
  .footer__logo img {
    width: 42px;
    height: 22px;
  }
}

.footer__nav {
  margin-right: auto;
}

.footer__nav-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
@media (max-width: 767px) and (max-width: 767px) {
  .footer__nav-list {
    flex-direction: column;
    align-items: flex-start;
  }
}

.footer__nav-item:not(:last-child) {
  margin-right: 33px;
}
@media (max-width: 1023px) and (max-width: 1023px) {
  .footer__nav-item:not(:last-child) {
    margin-right: 23px;
  }
}
@media (max-width: 767px) and (max-width: 767px) {
  .footer__nav-item:not(:last-child) {
    margin: 0 0 13px 0;
  }
}

@media (max-width: 767px) and (max-width: 767px) {
  .footer__nav-item {
    margin: 0 0 24px 0;
  }
}

.footer__nav-item a {
  position: relative;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 500;
  outline: none;
  color: #f9fbfd;
  transition: 0.2s ease-in;
}
@media (max-width: 1023px) and (max-width: 1023px) {
  .footer__nav-item a {
    font-family: "Montserrat", "Arial", sans-serif;
    font-size: 12px;
    line-height: 15px;
    font-weight: 500;
  }
}
.footer__nav-item a::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: -3px;
  width: 0;
  height: 2px;
  background-color: #005b74;
  transition: width 0.5s;
}
.footer__nav-item a:hover::after {
  content: "";
  width: 100%;
  position: absolute;
  left: 0;
  bottom: -3px;
  height: 2px;
  background-color: #0092bb;
  transition: width 0.5s;
}
.footer__nav-item a.focus-visible:active.focus-visible:active {
  color: #04191f;
}
.footer__nav-item a.focus-visible:focus.focus-visible:focus {
  outline: 2px solid #0092bb;
}

.social {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}
@media (max-width: 1023px) and (max-width: 1023px) {
  .social {
    width: 13%;
  }
}
@media (max-width: 767px) and (max-width: 767px) {
  .social {
    width: 92px;
    flex-wrap: wrap;
  }
}

.social__item {
  width: 20px;
  height: 20px;
  display: flex;
}
@media (max-width: 1023px) and (max-width: 1023px) {
  .social__item {
    width: 16px;
    height: 16px;
  }
}
.social__item.focus-visible:focus.focus-visible:focus {
  outline: 2px solid #0092bb;
}

.social__item a {
  cursor: pointer;
}
.social__item a.focus-visible:focus.focus-visible:focus {
  outline: 2px solid #0092bb;
}

.social__item:not(:last-child) {
  margin-right: 27px;
}
@media (max-width: 1023px) and (max-width: 1023px) {
  .social__item:not(:last-child) {
    margin-right: 19px;
  }
}
@media (max-width: 767px) and (max-width: 767px) {
  .social__item:not(:last-child) {
    margin-right: 18px;
  }
}

.social__item-icon {
  fill: #f9fbfd;
}
@media (max-width: 1023px) and (max-width: 1023px) {
  .social__item-icon {
    width: 16px;
    height: 16px;
  }
}
@media (hover: hover) and (hover: hover) {
  .social__item-icon:hover:not(.focus-visible):hover:not(.focus-visible) {
    fill: #00c8ff;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

